function FindOutMore() {
	const anC = useRef();

	useEffect(() => {
		gsap.from(anC.current, {
			delay: 4,
			y: -100,
			duration: 2,
			opacity: 0,
			ease: 'power3',
		});
	}, []);

	return (
		<div className="find-more hello">
			<button ref={anC} className="anC">
				more
			</button>
		</div>
	);
}

function ShowCase() {
	const triggerAbout = useRef(),
		anName = useRef(),
		anSub = useRef(),
		anB = useRef();

	useEffect(() => {
		const tl = gsap.timeline();
		const delay = 2;
		const stagger = 0.5;

		tl.from(anName.current, {
			duration: delay,
			opacity: 0,
			x: -1000,
			ease: 'power4',
		});
		tl.from(anB.current, {
			duration: delay / 2,
			opacity: 0,
			y: -100,
			stagger: stagger,
			ease: 'bounce',
		});
		tl.from(anSub.current, {
			duration: delay / 2,
			opacity: 0,
			y: -100,
			ease: 'bounce',
		});

		return function () {
			tl.kill();
		};
	}, []);

	return (
		<AboutWPDiv>
			<div ref={triggerAbout} className="triggerAbout" />
			<ShowCaseCon id="About">
				<AboutTitle>
					<div ref={anName} className="anName">
						<ShowH1>Hi, I'm Rick Gove</ShowH1>
					</div>
				</AboutTitle>
				<div ref={anB} className="anB">
					<ShowImg src="https://greensock.com/uploads/packages-0701062001407339578.png"></ShowImg>
				</div>
				<ShowSubhead ref={anSub} className="anSub">
					Junior Web Developer
				</ShowSubhead>
				<div>
					<FindOutMore />
					<MoreInfo />
				</div>
			</ShowCaseCon>
		</AboutWPDiv>
	);
}

